                    <router-link to="/">Inicio</router-link>
                    <router-link to="/about">Acerca de nosotros</router-link>
                    <router-link to="/history">Historia</router-link>
                    <router-link to="/contact">Contacto</router-link>
                </nav>
            </div>

        </div>

    </div>
</template>

<script>
import { ref } from 'vue';

export default {
    name: 'HeaderHome',
    setup () {       
        const screenSize = ref(true)
        const navDialog = ref(false);
        return {
            windowWidth: window.innerHeight,
            success: false,
            screenSize,
            navDialog,
        }
    },
    
    methods: {
        goAbout() {
            this.$router.push("/about");
        },
        closeDialog() {
            this.navDialog = !this.navDialog;
            console.log(this.screenSize);
        }

    },
    mounted() {
        window.onresize = () => {
            this.windowWidth = window.innerWidth
            console.log(this.windowWidth);

            if (this.windowWidth < 600) {
                this.screenSize = false;
                console.log(this.screenSize);
                //this.$router.go(0);
            } else {
                this.screenSize = true;
                console.log(this.screenSize);
                //this.$router.go(0);
            }
        }
    },

}
</script>

<style lang="scss" scoped>
    @import './scss/HeaderHome.scss';
